import "reflect-metadata";

import { CartItem } from "./CartItem";
import { classToPlain, plainToClass, Type } from "class-transformer";

export class CartOrder {
  @Type(() => CartItem)
  cartItems: CartItem[];
  discount: number;
  uid: string;
  orderId: string;
  quantity: number;
  discountCode: string;
  orderStatus: string;
  cost: number;
  createdDate: number;
  modifiedDate: number;
  orderType: string;
  pendingAmount: number;
  paidBy: string;
  deliveryStatus: string;
  paymentStatus: string;
  razorpayOrderId: string;
  paidOn: number;
  constructor() {
    this.cartItems = [];
    this.discount = 0;
    this.quantity = 0;
    this.cost = 0;
    this.createdDate = new Date().getTime();
    this.modifiedDate = new Date().getTime();
    this.discountCode = "";
    this.orderId = "";
    this.uid = "";
    this.orderStatus = "created";
    this.orderType = "";
    this.pendingAmount = 0;
    this.paidBy = "";
    this.deliveryStatus = "created";
    this.paymentStatus = "created";
    this.razorpayOrderId = "";
    this.paidOn = 0;
  }
  get totalCost() {
    let total = 0;
    for (const item of this.cartItems) {
      total += item.totalCost;
    }
    return total;
  }
  set totalCost(cost: number) {
    this.totalCost = cost;
  }
  static converter = {
    toFirestore(expense: CartOrder): firebase.firestore.DocumentData {
      return classToPlain(expense);
    },
    fromFirestore(
      snapshot: firebase.firestore.QueryDocumentSnapshot,
      options: firebase.firestore.SnapshotOptions
    ): CartOrder {
      const data = snapshot.data(options)!;
      return plainToClass(CartOrder, data);
    }
  };
}
