import FireUser from "./FireUser";
import { ProductInfo } from "./ProductInfo";
import SellerDetails from "./SellerDetails";
import { v4 as uuidv4 } from "uuid";
import { classToPlain, plainToClass } from "class-transformer";
import { PlatformDetails } from "./PlatformDetails";
import { DeliveryStatus } from "./DeliveryStatus";

export class CartItem {

  id!: string;
  cost: number;
  quantity: number;
  productId: string;
  productName: string;
  headerImageUrl: string;
  sellerPriceId: string;
  sellerId: string;
  sellerName: string;
  sellerLogo: string;
  platformDetails: PlatformDetails[];
  isDigitalProduct = false;
  redeemed = false;
  productKey: string;
  deliveryStatus: string;
  waitForKey = false;
  waitPeriod: number;
  trackingUrl: string;
  constructor() {

    this.id = uuidv4();
    this.quantity = 0;
    this.cost = 0;
    this.productId = "";
    this.productName = "";
    this.headerImageUrl = "";
    this.sellerPriceId = "";
    this.sellerId = "";
    this.platformDetails = [];
    this.sellerName = "";
    this.sellerLogo = "";
    this.productKey = "";
    this.deliveryStatus = DeliveryStatus.AWAITED;
    this.waitPeriod = 0;
    this.trackingUrl = "";
  }
  get totalCost() {
    return this.cost * this.quantity;
  }
  static converter = {
    toFirestore(expense: CartItem): firebase.firestore.DocumentData {
      return classToPlain(expense);
    },
    fromFirestore(
      snapshot: firebase.firestore.QueryDocumentSnapshot,
      options: firebase.firestore.SnapshotOptions
    ): CartItem {
      const data = snapshot.data(options)!;
      return plainToClass(CartItem, data);
    }
  };
}
