import { Component, Prop, Vue } from "vue-property-decorator";

import { FirestoreService } from "@/services/FirestoreService";
import { auth, functions } from "@/firebaseInit";
import { CartOrder } from "@/dto/CartOrder";
import { CartItem } from "@/dto/CartItem";
import UserDetails from "@/components/UserDetails/UserDetails.vue";
import FlipCountdown from 'vue2-flip-countdown'
import format from "date-fns/format";
import Swal from "sweetalert2";
import { DeliveryStatus } from "@/dto/DeliveryStatus";
import { OrderType } from "@/dto/OrderType";

@Component({ components: { UserDetails, FlipCountdown } })
export default class OrderDetails extends Vue {
  steppers: number[] = [];
  cashOrder = false;
  deliveryStatus = DeliveryStatus;
  order: CartOrder = new CartOrder();
  keyDialog = false;
  productKey = "";
  selectedCartItem: CartItem = new CartItem();
  loading = false;
  infoLoading = false;
  noOrders = false;
  headers: any = [
    {
      text: "ORDERED PRODUCTS",
      value: "headerImageUrl",
      width: "33%",
      sortable: false
    },
    {
      text: "DELIVERY STATUS",
      value: "orderStatus",
      width: "33%",
      sortable: false
    },

    { text: "Actions", value: "actions", sortable: false, width: "33%" }
  ];
  redeemModal = false;
  timeDialog = false;
  extendedTime = 0;
  async mounted() {
    await this.loadOrder();
  }
  items: any[] = [
    {
      exact: true,
      text: "Orders",
      disabled: false,
      to: "/orders"
    },
    {
      text: "Order",
      disabled: true
    }
  ];
  formatDate(waitPeriod: any) {
    return format(new Date(waitPeriod), "yyyy-MM-dd HH:mm:ss");
  }
  async loadOrder() {
    try {
      this.infoLoading = true;
      console.log(this.$route.params.id)
      const userDetails = auth.currentUser;
      if (userDetails) {
        const resp = await FirestoreService.getSingleDocument(
          "orders",
          this.$route.params.id,
          CartOrder.converter
        );
        this.order = resp.data();
        this.buildSteppers(this.order.cartItems);
        if (this.order.orderType === OrderType.CASH) {
          this.cashOrder = true;
        } else {
          this.cashOrder = false;
        }
      } else {
        Vue.toasted.show("User not logged in", {
          icon: "close-circle",
          type: "error"
        });
      }
    } catch (error) {
      console.log(error);
      Vue.toasted.show(error, { icon: "close-circle", type: "error" }); this.noOrders = true;
    } finally {
      this.infoLoading = false;
    }
  }
  async processKey(order: CartItem) {
    try {
      if (order.isDigitalProduct && !order.redeemed) {
        console.log("not redeemed");
        this.$router.push("/order-details/" + this.order.orderId);
      } else if (order.isDigitalProduct && order.redeemed) {
        this.redeemModal = true;
        this.selectedCartItem = order;
      }
    } catch (error) {
      console.log(error);
    }
  }
  async viewAddKeyModal(order: CartItem) {
    try {
      this.keyDialog = true;
      this.selectedCartItem = order;
    } catch (error) {
      console.log(error);
    }
  }
  async viewTimeModal(order: CartItem) {
    try {
      this.timeDialog = true;
      this.selectedCartItem = order;
    } catch (error) {
      console.log(error);
    }
  }
  async copyKeyToClipboard() {
    try {
      await navigator.clipboard.writeText(this.selectedCartItem.productKey);
      Vue.toasted.show("key copied to clipboard", {
        icon: "close-circle",
        type: "success"
      });
    } catch (error) {
      console.log(error);
      Vue.toasted.show(error.message, { icon: "close-circle", type: "error" });
    }
  }
  close() {
    this.keyDialog = false
  }
  closeTimeModal() {
    this.timeDialog = false
  }
  async addKey() {
    try {
      this.keyDialog = false;
      const res = await Swal.fire({
        title: 'Are you sure?',
        text: "This cant be undone!",
        icon: 'warning',
        allowEscapeKey: false,
        allowOutsideClick: false,
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, Add Manual Key!',
        cancelButtonText: 'No, cancel!',
        showLoaderOnConfirm: true,
        preConfirm: async () => {
          try {
            const keyReqPayload = {
              key: this.productKey,
              productId: this.selectedCartItem.productId,
              orderId: this.order.orderId,
              sellerPriceId: this.selectedCartItem.sellerPriceId,
              sellerId: this.selectedCartItem.sellerId,
              sellerName: this.selectedCartItem.sellerName,
              cartItemId: this.selectedCartItem.id,
              uid: this.order.uid
            };
            const redeemManualKey = functions.httpsCallable("redeemManualKey");
            const resp = await redeemManualKey(keyReqPayload);
            console.log(resp.data);
            return resp.data;
          } catch (error) {
            Swal.showValidationMessage(
              `Request failed: ${error}`
            )
          }
        },
      });
      if (res.isConfirmed) {
        Swal.fire(
          'Operation Successful',
          'Key have been added to the order',
          'success',

        )
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    }

  }
  async addTime() {
    try {
      this.timeDialog = false;
      const res = await Swal.fire({
        title: 'Are you sure?',
        text: "This cant be undone!",
        icon: 'warning',
        allowEscapeKey: false,
        allowOutsideClick: false,
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, Add Extend Time!',
        cancelButtonText: 'No, cancel!',
        showLoaderOnConfirm: true,
        preConfirm: async () => {
          try {
            const resp = await FirestoreService.getSingleDocument(
              "orders",
              this.$route.params.id,
              CartOrder.converter
            );
            this.order = resp.data();

            for (let i = 0; i < this.order.cartItems?.length; i++) {
              if (this.order.cartItems[i].sellerPriceId === this.selectedCartItem.sellerPriceId) {
                const waitPeriod = this.addHoursToDate(this.order.cartItems[i].waitPeriod, this.extendedTime);
                this.order.cartItems[i].waitPeriod = waitPeriod;
              }
            }
            const extendTimeResponse = await FirestoreService.setOrMergeDocument(
              "orders",
              this.order.orderId, this.order,
              CartOrder.converter
            );
            return extendTimeResponse;
          } catch (error) {
            Swal.showValidationMessage(
              `Request failed: ${error}`
            )
          }
        },
      });
      if (res.isConfirmed) {
        Swal.fire(
          'Operation Successful',
          'Time hase been extend by ' + this.extendedTime + ' hours',
          'success',

        )
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });
    }

  }
  buildSteppers(cartItems: CartItem[]) {
    try {
      this.steppers = [];
      for (const cartItem of cartItems) {
        let num = 1;
        if (cartItem.deliveryStatus === DeliveryStatus.IN_PROGRESS) {
          num = 1;
        } else if (cartItem.deliveryStatus === DeliveryStatus.SHIPPED) {
          num = 2;
        } else if (cartItem.deliveryStatus === DeliveryStatus.DELIVERED) {
          num = 3;
        }
        this.steppers.push(num);

      }
    } catch (error) {
      console.log(error);
    }
  }
  async markShipped(item: CartItem) {
    try {
      this.keyDialog = false;
      const res = await Swal.fire({
        title: 'Are you sure?',
        text: "This cant be undone!",
        icon: 'warning',
        input: 'url',
        inputPlaceholder: 'Enter Shipping URL',
        allowEscapeKey: false,
        allowOutsideClick: false,
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, Mark as shipped!',
        cancelButtonText: 'No, cancel!',
        showLoaderOnConfirm: true,
        preConfirm: async (url: string) => {
          try {
            const keyReqPayload = {
              trackingUrl: url,
              orderId: this.order.orderId,
              sellerPriceId: item.sellerPriceId,
            };
            const redeemManualKey = functions.httpsCallable("markShipped");
            const resp = await redeemManualKey(keyReqPayload);
            console.log(resp.data);
            return resp.data;
          } catch (error) {
            Swal.showValidationMessage(
              `Request failed: ${error}`
            )
          }
        },
      });
      if (res.isConfirmed) {
        Swal.fire(
          'Operation Successful',
          'Product marked as shipped',
          'success',

        )
        await this.loadOrder();
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    }
  }
  async markDelivered(item: CartItem) {
    try {
      this.keyDialog = false;
      const res = await Swal.fire({
        title: 'Are you sure?',
        text: "This cant be undone!",
        icon: 'warning',
        allowEscapeKey: false,
        allowOutsideClick: false,
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, Mark as Delivered!',
        cancelButtonText: 'No, cancel!',
        showLoaderOnConfirm: true,
        preConfirm: async () => {
          try {
            const keyReqPayload = {
              orderId: this.order.orderId,
              sellerPriceId: item.sellerPriceId,
            };
            const redeemManualKey = functions.httpsCallable("markDelivered");
            const resp = await redeemManualKey(keyReqPayload);
            console.log(resp.data);
            return resp.data;
          } catch (error) {
            Swal.showValidationMessage(
              `Request failed: ${error}`
            )
          }
        },
      });
      if (res.isConfirmed) {
        Swal.fire(
          'Operation Successful',
          'Product marked as Delivered',
          'success',
        )
        await this.loadOrder();
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    }
  }
  addHoursToDate(objDate: number, intHours: number) {
    const addMlSeconds = (intHours * 60) * 60 * 1000;
    return objDate + addMlSeconds;

  }
  async validateKeyForm() {
    if ((this.$refs.keyForm as Vue & { validate: () => boolean }).validate()) {
      await this.addKey();
    }
  }
  async validateTimeForm() {
    if ((this.$refs.timeForm as Vue & { validate: () => boolean }).validate()) {
      await this.addTime();
    }
  }
}
