import { classToPlain, plainToClass } from "class-transformer";
import Address from "./Address";

export default class FireUser {
  uid: string;
  displayName: string;
  email: string;
  emailVerified: boolean;
  mobileVerified: boolean;
  storeCredits?: boolean;
  photoURL: string;
  mobileNumber: string;
  providerId?: string;
  otp: number;
  fcmToken: string;
  address: Address;
  admin: boolean | undefined;
  superAdmin: boolean | undefined;
  constructor() {
    this.uid = '';
    this.displayName = '';
    this.email = '';
    this.emailVerified = false;
    this.mobileVerified = false;
    this.photoURL = '';
    this.mobileNumber = '';
    this.otp = 0;
    this.fcmToken = '';
    this.address = new Address();
  }
  static converter = {
    toFirestore(expense: FireUser): firebase.firestore.DocumentData {
      return classToPlain(expense);
    },
    fromFirestore(
      snapshot: firebase.firestore.QueryDocumentSnapshot,
      options: firebase.firestore.SnapshotOptions
    ): FireUser {
      const data = snapshot.data(options)!;
      return plainToClass(FireUser, data);
    }
  };
}
