import { Component, Prop, Vue } from "vue-property-decorator";


import { FirestoreService } from '@/services/FirestoreService';
import Swal from 'sweetalert2';

import Invoicing from '@/components/Invoicing/Invoicing.vue'
import { InvoiceTemplate } from "@/templates/InvoiceTemplate";
import Invoice from "@/dto/ItemsCheckout";
import OrderDetails from '@/components/OrderDetails/OrderDetails.vue'
import { ProductInfo } from '@/dto/ProductInfo';
import { CartOrder } from "@/dto/CartOrder";
import { OrderStatus } from "@/dto/OrderStatus";
import { firestore } from "@/firebaseInit";
import moment from "moment";
import { format, parseISO } from "date-fns";
import FireUser from "@/dto/FireUser";

@Component
export default class Users extends Vue {

  stats: any = {};
  sellerSearchLoading = false;
  userList: FireUser[] = [];
  loading = false;
  searchDisabled = false;
  userNameString = "";
  userIdString = "";
  userEmailString = "";
  userMoblieString = "";
  dateMenu = false;
  searchLoading = false;
  orderStatus: string[] = Object.keys(OrderStatus);
  headers: any = [
    { text: 'User ID', value: 'uid', },
    { text: 'Name ', value: 'displayName' },
    { text: 'Email', value: 'email' },
    { text: 'Mobile', value: 'mobileNumber' },
    {
      text: 'User Status', value: 'admin'
    },
    {
      text: 'Provider', value: 'providerId'
    },
    { text: 'Actions', value: 'actions', sortable: false },
  ]
  editedIndex = 0;
  editedItem: ProductInfo = new ProductInfo();
  dialog = false;
  statsLoading = false;
  invoiceDate = format(parseISO(new Date().toISOString()), 'yyyy-MM-dd');
  async mounted() {
    await this.searchProducts();
  }
  async searchProducts() {
    console.log('userNameString:' + this.userNameString)
    console.log('userIdString:' + this.userIdString)
    console.log('userMoblieString:' + this.userMoblieString)
    console.log('userEmailString:' + this.userEmailString)

    await this.searchQueryString();
    // } else {
    //   await this.getProducts();
    // }
  }
  async loadStats() {
    try {
      this.statsLoading = true;
      const resp = await FirestoreService.getSingleDocument(
        "Stats",
        "orders");
      this.stats = resp.data();
      Object.keys(this.stats).map(key => {
        if (this.snakeCaseToCamelCase(key.toUpperCase()) != key) {
          this.stats[this.snakeCaseToCamelCase(key.toUpperCase())] = this.stats[key];
          delete this.stats[key];
        }
      });

    } catch (error) {
      console.log(error);
      Vue.toasted.show(error, { icon: "close-circle", type: "error" });
    } finally {
      this.statsLoading = false;
    }
  }
  async getProducts() {
    try {
      this.userList = [];
      this.sellerSearchLoading = true;
      const snapshot = await FirestoreService.getDocuments('orders', CartOrder.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.userList.push(doc.data());
        });
        Vue.toasted.show('Search complete', { icon: 'face', type: 'success' });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }


    } catch (error) {
      console.log(error)

    }
    finally {
      this.sellerSearchLoading = false;
    }

  }
  editItem(item: CartOrder) {
    this.$router.push({ name: 'user-details', params: { id: item.uid } });
  }
  snakeCaseToCamelCase(str: string) {
    return str.replace(/_/g, ' ');
  }

  async deleteItem(item: FireUser) {
    try {
      const res = await Swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!',

      });
      if (res.isConfirmed) {
        await FirestoreService.deleteDocument('users', item.uid);
        const deleteIndex = this.userList.indexOf(item)

        this.userList.splice(deleteIndex, 1)
        Swal.fire(
          'Deleted!',
          'Seller has been deleted.',
          'success'
        )
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    }

  }
  async searchQueryString() {
    try {
      this.searchDisabled = true;
      this.searchLoading = true;
      this.userList = [];
      const baseQuery = firestore
        .collection('users')
        .withConverter(FireUser.converter);
      let query: any = baseQuery;
      if (this.userNameString) {
        query = query.where('displayName', '>=', this.userNameString)
          .where('displayName', "<=", this.userNameString + "\uf8ff")
      }
      if (this.userIdString) {
        query = query.where('uid', '==', this.userIdString);
      }
      if (this.userMoblieString) {
        query = query.where('mobileNumber', '==', this.userMoblieString);
      }
      if (this.userEmailString) {
        query = query.where('email', '==', this.userEmailString);
      }
      const snapshot = await query.limit(20).get();
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.userList.push(doc.data());
        });
        Vue.toasted.show('Found ' + this.userList.length + ' items', { icon: 'face', type: 'success' });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }

    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    } finally {
      this.searchLoading = false;
      this.searchDisabled = false;


    };
  }
  get invoiceDateFormatted() {
    return this.invoiceDate ? moment(this.invoiceDate).format('DD/MM/YYYY') : '';
  }
}
